.cart-footer{
  width: 100%;
  height: 30%;
}
.total-amount{
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
hr{
  border: none;
  height: 2px;
  background: black;
  width: 40%;
}
.checkout{
  bottom: 0;
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.checkout div {
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 80%;
  border-radius: 16px;
  box-shadow: 1px 1px 7px 1px black;
  background: #bda98f;
  letter-spacing: 2px;
  font-weight: 600;
}